import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { mjways2Mul } from './mjways2Mul';
import { mjways2 } from './cMjways2';

const { ccclass, property } = _decorator;

@ccclass('mjways2Bg')
export class mjways2Bg extends Component {
    @property({ type: mjways2Mul, tooltip: "倍数" })
    public mul: mjways2Mul = null!;
    @property({ type: [Node], tooltip: "背景节点" })
    public nodes: Node[] = [];
    onLoad() {

    }

    protected start(): void {
        this.init();
    }

    init() {
        this.fortuneBn = false;
        mjways2.bg = this;
    }

    /**是否启用幸运模式 */
    private _fortuneBn: boolean = false;
    public get fortuneBn(): boolean {
        return this._fortuneBn;
    }
    public set fortuneBn(v: boolean) {
        this._fortuneBn = v;
        this.mul.fortuneBn = v;
        this.doNodes((node: Node) => {
            node.children[0].active = v;
        })
        if (v == false) mjways2.slotCtrl.lastNum = -1;
        mjways2.audio.playBgm(v?1:0);
    }

    private doNodes(func: Function) {
        for (let i = 0; i < this.nodes.length; i++) {
            func(this.nodes[i], i);
        }
    }
}