import { _decorator, Component, macro, sp } from 'cc';
import { Node } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Vec3 } from 'cc';
import { Label } from 'cc';
import { tween } from 'cc';
import { bezierMove } from '../../module_basic/utils/bezierMove';
import { NodePool } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { mjways2 } from './cMjways2';
import { Sprite } from 'cc';
import { SpriteFrame } from "cc";
const { ccclass, property } = _decorator;

@ccclass('mjways2Bigwin')
export class mjways2Bigwin extends Component {
    @property({ type: Sprite, tooltip: "" })
    public icon: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "" })
    public icons: SpriteFrame[] = [];
    @property({ type: Node, tooltip: "" })
    public small: Node = null!;
    @property({ type: Node, tooltip: "" })
    public big: Node = null!;
    @property({ type: Label, tooltip: "coin label" })
    public coinLabel: Label = null!;
    @property({ type: LanguageSprite, tooltip: "" })
    public bigWinBw: LanguageSprite = null!;

    onLoad() {
    }

    protected start(): void {
        this.init();
    }
    private init() {
        this.coin = 0;
        this.coinLabel.string = "0";
        this.small.active = true;
        this.big.active = false;
    }

    //大奖数字滚动动画
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverBn: boolean = true;
    public get playOverBn(): boolean {
        return this._playOverBn;
    }
    public set playOverBn(v: boolean) {
        this._playOverBn = v;
    }

    private _coin: number = 0;
    public get coin(): number {
        return this._coin;
    }
    public set coin(v: number) {
        this._coin = v;
    }

    bigwincoin(coin: number) {
        this.node.parent = mjways2.main.node;
        mjways2.audio.playBigwin();
        this.node.active = true;
        this.coin = coin;
        this.playBigWinCoin();
    }

    private playBigWinCoin() {
        let coin = this.coin;
        this.node.active = true;
        this.bigBn = false;
        this.superBn = false;
        this.megaBn = false;
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / 100;
        this.playOverBn = false;
        this.coinLabel.string = Num.exchangeToThousands(mjways2.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.closeBigWin, 16);
    }

    private bigBn: boolean = false;
    private superBn: boolean = false;
    private megaBn: boolean = false;
    private coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playBigWin(this.aimCoin);
            this.playCoinOver();
        } else {
            this.playBigWin(this.addcoin);
            this.coinLabel.string = Num.exchangeToThousands(mjways2.exchangeRate, this.addcoin);
        }
    }

    private playCoinOver() {
        this.unschedule(this.coinOneChange);
        mjways2.audio.playBigwinEnd();
        this.playOverBn = true;
        this.coinLabel.string = Num.exchangeToThousands(mjways2.exchangeRate, this.aimCoin);
        this.unschedule(this.closeBigWin);
        this.scheduleOnce(this.closeBigWin, 5);
    }

    private playBigWin(coin: number) {
        if (coin >= mjways2.betSum * 35 && this.megaBn == false) {
            this.playAnim_MegaWin();
            this.megaBn = true;
        } else if (coin >= mjways2.betSum * 15 && coin < mjways2.betSum * 35 && this.superBn == false) {
            this.playAnim_SuperWin();
            this.superBn = true;
        } else if (coin < mjways2.betSum * 15 && this.bigBn == false) {
            this.playAnim_BigWin();
            this.bigBn = true;
        }
    }

    onFastBigWin(e: TouchEvent) {
        if (this.playOverBn) {
            this.closeBigWin();
        } else {
            this.playCoinOver();
        }
    }

    //大奖BigWin动画
    private playAnim_BigWin() {
        this.bigWinBw.dataID = "bw_text";
        this.icon.spriteFrame = this.icons[0];
    }

    //大奖SuperWin动画
    private playAnim_SuperWin(coin: number = 0) {
        this.bigWinBw.dataID = "mw_text";
        this.icon.spriteFrame = this.icons[1];
        this.big.active = true;
    }

    //大奖MegaWin动画
    private playAnim_MegaWin(coin: number = 0) {
        this.bigWinBw.dataID = "smw_text";
        this.icon.spriteFrame = this.icons[2];
    }

    closeBigWin() {
        this.unschedule(this.closeBigWin);
        mjways2.audio.stopBigwinEnd();
        mjways2.main.closeBigWin();
        this.node.destroy();
        mjways2.bigwin = undefined;
    }
}