import { _decorator, Component, Node } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { labNumShow } from '../../module_basic/utils/labNumShow';
import { mjways2 } from './cMjways2';
import { Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('mjways2FreeEnd')
export class mjways2FreeEnd extends Component {
    @property({ type: Node, tooltip: "金额" })
    public coin: Node = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;

    onLoad() {

    }

    protected start(): void {
        this.clickBn = false;
    }

    setParent(coin: number) {
        this.node.parent = mjways2.slotCtrl.node;
        let coinNum: labNumShow = this.coin.getComponent(labNumShow);
        coinNum.num = 0;
        this.btn.active = false;
        this.bindCoin(coin);
    }

    private bindCoin(coin: number) {
        let coinNum: labNumShow = this.coin.getComponent(labNumShow);
        tween(coinNum)
            .to(4, { num: coin * 100 })
            .call(this.showBtn, this)
            .start();
    }

    private showBtn() {
        this.btn.active = true;
        this.btn.scale = new Vec3(0.1, 0.1)
        tween(this.btn)
            .to(0.5, { scale: new Vec3(0.8, .8) })
            .start();
        this.scheduleOnce(this.close, 5);
    }

    private _clickBn: boolean;
    public get clickBn(): boolean {
        return this._clickBn
    }
    public set clickBn(v: boolean) {
        this._clickBn = v;
        this.btn.children[0].getComponent(Sprite).enabled = !v;
        this.btn.children[0].children[0].active = v;
    }

    private close() {
        this.node.destroy();
        mjways2.bg.fortuneBn = false;
        mjways2.main.freeEndBack();
    }

    private onClose() {
        if (this.btn.active) {
            this.clickBn = true;
            this.scheduleOnce(() => {
                this.close();
            }, 0.5)
        }
    }
}