/*
histroy
*/
import { _decorator, Component, Prefab, Node, ScrollView } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { instantiate } from 'cc';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { ApiReturn } from 'tsrpc-browser';
import { ResPlayMatch3 } from '../../module_basic/shared/protocols/public/slots/PtlPlayMatch3';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { mjways2Audio } from './mjways2Audio';
import { mjways2 } from './cMjways2';
import { mjways2FreeEnd } from './mjways2FreeEnd';
import { LanguageData } from '../../module_basic/scripts/language/LanguageData';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
import { Button } from 'cc';
import { mjways2Symbol } from './mjways2Symbol';
import { dataChange, Round } from './dataChange';
import { mjways2FreeStart } from './mjways2FreeStart';
import { Animation } from 'cc';
import { mjways2Detail } from './mjways2Detail';
import { macro } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';

const { ccclass, property } = _decorator;

@ccclass('mjways2Main')
export class mjways2Main extends Component {
    @property({ type: Prefab, tooltip: "大奖" })
    public bigwin: Prefab = null!;
    @property({ type: Prefab, tooltip: "免费开始" })
    public freeStart: Prefab = null!;
    @property({ type: Prefab, tooltip: "免费结束" })
    public freeEnd: Prefab = null!;
    @property({ type: Prefab, tooltip: "角色详情Pb" })
    public detailPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "幸运模式滚动开始动画" })
    public fortuneAni: Prefab = null!;
    @property({ type: Node, tooltip: "" })
    public slotCtrlNode: Node = null!;
    @property({ type: Node, tooltip: "" })
    public dark: Node = null!;
    public delayClick: boolean = false;

    onLoad() {
        mjways2.main = this;
    }

    start() {
        this.dark.active = false;
        mjways2.rollEnable = true;
    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                this.add();
                break;
            case "dec":
                this.dec();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                break;
            case "lastmoney":
                mjways2.lastmoneyPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                mjways2.main.close();
                RoomMgr.inst.exitRoom();
            }
        });
    }

    startRoll() {
        if (this.delayClick || mjways2.rollEnable == false) {
            return;
        }
        if (!mjways2.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (mjways2.status == 0) {
                mjways2.status = 2;
                this.sendRoll();
            } else if (mjways2.status == 1) {
                mjways2.slotCtrl.setSpinAnim(3);
                this.stopImmediately();
            }
        }

    }

    add() {
        if (mjways2.auto) {
            return;
        }
        mjways2.sumIdx += 1;
        mjways2.sumIdx = mjways2.slotCtrl.updateBet(mjways2.sumIdx) == 0 ? mjways2.sumIdx - 1 : mjways2.sumIdx;
        tween(mjways2.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    dec() {
        if (mjways2.auto) {
            return;
        }
        mjways2.sumIdx -= 1;
        mjways2.sumIdx = mjways2.slotCtrl.updateBet(mjways2.sumIdx) == 0 ? mjways2.sumIdx + 1 : mjways2.sumIdx;
        tween(mjways2.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    startAuto(n: any) {
        if (this.delayClick) {
            return;
        }
        if (mjways2.status == 0) {
            mjways2.auto = true;
            mjways2.autoTimes = n;
            (((mjways2.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((mjways2.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
                ((mjways2.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((mjways2.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
                ((mjways2.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((mjways2.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
                ((mjways2.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(1, 1, 1);
            }
            mjways2.slotCtrl.Btn_stopAuto.active = mjways2.auto;
            mjways2.slotCtrl.Btn_start.active = !mjways2.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1.5);
        }
    }

    stopAuto() {
        mjways2.auto = false;
        mjways2.autoTimes = 0;
    }

    /**滚轮回调，所有滚轮滚动完毕触发 */
    private rollCallBackBn: boolean = false;
    rollCallBack() {
        let st = 0;
        mjways2.status = st;
        if (mjways2.status == 0) {
            if (this.rollCallBackBn) {
                return;
            }
            this.rollCallBackBn = true;
            this.scheduleOnce(() => {
                this.rollCallBackBn = false;
            }, 1)
            mjways2.reel.removeRadom();
            if (mjways2.result.winCoin > 0) {
                mjways2.slotCtrl.setSpinAnim(3);
            } else {
                mjways2.slotCtrl.setSpinAnim(2);
            }
            this.scheduleOnce(() => {
                let arr: mjways2Symbol[] = mjways2.reel.getSymbols as mjways2Symbol[];
                for (let i = 0; i < arr.length; i++) {
                    arr[i].spawn();
                }
                this.roundShow();
            }, 1)
        }
    }

    private roundShow() {
        let round: Round = mjways2.round;
        if (round.removeItems.length > 0) {
            this.showRemove();
            if (round.removeType) {
                mjways2.audio.playVoxPayout(round.removeType - 1);
            } else {
                mjways2.audio.playVoxPayoutMultiSym();
            }
        } else {
            this.settlement();
        }
    }

    private showRemove() {
        let remove: number[] = mjways2.round.removeItems;
        if (remove.length > 1) {
            let arr: mjways2Symbol[] = mjways2.reel.getSymbols as mjways2Symbol[];
            for (let i = 0; i < remove.length; i++) {
                let index: number = remove[i];
                arr[index].playWin();
            }
            if (mjways2.round.winCoin > 0) {
                mjways2.infoBoard.playAnimWin(mjways2.round.winCoin, true);
            }
            this.scheduleOnce(this.doRemove, 3);
        } else {
            this.settlement();
        }
    }

    private doRemove() {
        mjways2.reel.drop();
    }

    dropBack() {
        this.doNext();
    }

    private settlement() {
        this.roundSM();
    }

    private roundSM() {
        if (mjways2.round.winCoin > 0) {
            mjways2.infoBoard.playAnimWin(mjways2.round.winCoin, false);
            this.scheduleOnce(this.roundSMNext, 3);
        } else {
            this.roundSMNext();
        }
    }

    private roundSMNext() {
        if (mjways2.roundIndex >= mjways2.result.rounds.length - 1) {
            if (mjways2.result.winCoin > 0) {
                if (mjways2.result.winCoin < 5 * mjways2.betSum) {
                    if (mjways2.result.winCoin <= 3 * mjways2.betSum) {
                        //  mjways2.main.audio.playPrizeSmallwin();
                    } else {
                        //  mjways2.main.audio.playPrizeMedwinCounting();
                    }
                    mjways2.infoBoard.playAnimWin(mjways2.result.winCoin, true, true);
                    this.scheduleOnce(this.doRoundSMNext, 2);
                } else {
                    mjways2.bigwin.bigwincoin(mjways2.result.winCoin);
                }
            } else {
                this.doRoundSMNext();
            }
        }
    }

    private doRoundSMNext() {
        if (mjways2.resultIndex == 0 && mjways2.roundIndex == mjways2.result.rounds.length - 1 && mjways2.fortuneBn) {
            let freeStart: mjways2FreeStart = instantiate(this.freeStart).getComponent(mjways2FreeStart);
            freeStart.setParent();
        } else {
            if (mjways2.round.addItems.length == 0) {
                this.doNext();
            }
        }
    }

    freeStartBack() {
        mjways2.bg.mul.playFortune();
    }

    playFortuneBack() {
        this.doNext();
    }

    freeEndBack() {
        this.playRollOver();
    }

    private doNext() {
        if (mjways2.roundIndex < mjways2.result.rounds.length - 1) {
            this.doNextRound();
        } else {
            this.doNextResult();
        }
    }

    //继续下回合表现
    private doNextRound() {
        mjways2.roundIndex++;
        this.roundShow();
    }

    //当前次数回合数据表现完毕，进入下一次摇奖表现
    private doNextResult() {
        mjways2.infoBoard.showInfosBn = true;
        mjways2.roundIndex = 0;
        mjways2.resultIndex++;
        if (mjways2.playOverBn) {
            if (mjways2.lotteryRes.results.length == 1) {
                this.playRollOver();
            } else {
                let free: mjways2FreeEnd = instantiate(this.freeEnd).getComponent(mjways2FreeEnd);
                free.setParent(mjways2.lotteryRes.winCoin);
            }
            return;
        }
        mjways2.slotCtrl.lastNum = mjways2.result.leftFreeCount - 1;
        mjways2.reel.loop();
        let loopEnd: Function = () => {
            let delay: number = 1;
            this.scheduleOnce(() => {
                mjways2.reel.loopEnd(mjways2.round.symbols, mjways2.round.golds, () => {
                    this.rollCallBack();
                });
            }, delay)
        }
        if (mjways2.inFortune && mjways2.preResult.winCoin > 0) {
            let fortuneAni: Animation = instantiate(this.fortuneAni).getComponent(Animation);
            fortuneAni.node.parent = mjways2.reel.node.parent;
            fortuneAni.on("finished" as any, () => {
                loopEnd();
                fortuneAni.node.destroy();
            })
            this.scheduleOnce(() => {
                fortuneAni.play();
                mjways2.audio.playFsDragonFly();
            }, 1);
        } else {
            loopEnd();
        }
    }

    closeBigWin() {
        mjways2.infoBoard.playAnimWin(mjways2.result.winCoin, false, true);
        if (mjways2.resultIndex == 0 && mjways2.roundIndex == mjways2.result.rounds.length - 1 && mjways2.fortuneBn) {
            let freeStart: mjways2FreeStart = instantiate(this.freeStart).getComponent(mjways2FreeStart);
            freeStart.setParent();
        } else {
            this.doNext();
        }
    }

    /**单次滚动完成结算 */
    playRollOver() {
        this.rollComplete();
        this.doAuto();
    }

    doAuto() {
        if (mjways2.auto && mjways2.autoTimes > 0) {
            this.sendRoll();
        }
    }
    /**滚动完成 */
    private rollComplete() {
        mjways2.status = 0;
        mjways2.slotCtrl.setSpinAnim(0);
        this.addDetailListener();
    }

    roll(list: number[], golds: number[]) {
        if (!mjways2.auto) {
            mjways2.rollEnable = true;
        }
        mjways2.status = 1;

        this.scheduleOnce(() => {
            mjways2.reel.loopEnd(list, golds, () => { this.rollCallBack() });
        }, 0.5);
    }



    sendRoll() {
        this.initRoll();
        mjways2.autoTimes = mjways2.autoTimes > 0 ? mjways2.autoTimes - 1 : 0;
        if (mjways2.autoTimes == 0) {
            mjways2.auto = false;
        }
        mjways2.slotCtrl.setSpinAnim(1);
        let autoTimesLab: Label = mjways2.slotCtrl.Btn_stopAuto.children[0].getComponent(Label) as Label;
        autoTimesLab.string = mjways2.autoTimes + "";
        // this.audio.playclickstart();
        if (mjways2.autoTimes > 999) {
            mjways2.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.5, 0.5);
        } else if (mjways2.autoTimes > 99) {
            mjways2.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.64, 0.64);
        }
        else {
            mjways2.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1);
        }
        mjways2.reel.loop();
        this.sendLottery();
    }

    firstSymbolOver() {
        mjways2.reel.spawn();
        this.addDetailListener();
    }

    private addDetailListener() {
        let symbols: baseSymbol[] = mjways2.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = true;
        }
    }

    private removeDetailListener() {
        let symbols: baseSymbol[] = mjways2.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.darkBn = false;
            symbol.node.getComponent(Button).interactable = false;
        }
        this.dark.active = false;
        let detail: mjways2Detail = mjways2.detail;
        if (detail) detail.node.active = false;
    }

    addDetail(s: baseSymbol) {
        if (!mjways2.rollEnable) return;
        let detail: mjways2Detail = mjways2.detail;
        if (!detail) {
            detail = instantiate(this.detailPb).getComponent(mjways2Detail);
            detail.node.parent = this.node;
            mjways2.detail = detail;
        }
        detail.node.active = true;
        let symbols: baseSymbol[] = mjways2.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.poolId != s.poolId) {
                symbol.darkBn = true;
            }
        }
        this.dark.active = true;
        detail.node.position = s.node.position;
        detail.node.y += mjways2.reel.node.parent.y;
        detail.init(s as mjways2Symbol);
    }

    /**每次滚动初始化场景 */
    initRoll() {
        this.removeDetailListener();
        mjways2.infoBoard.showInfosBn = true;
        mjways2.tempWin = 0;
        mjways2.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways2.exchangeRate, mjways2.money);
        mjways2.slotCtrl.winCoin_lab.string = Num.exchangeToThousands(mjways2.exchangeRate, 0);
    }

    async sendLottery() {
        let retSpin: ApiReturn<ResPlayMatch3> = await gameNet.PlayMatch3(SubGameDef.SLOTS_MJWAYS2, mjways2.betSum + "");
        console.log(retSpin);
        this.lotteryBack(retSpin);
    }

    private lotteryBack(data: any) {
        if (data && data.isSucc) {
            this.exchange(data.res as ResPlayMatch3);
            console.log(mjways2.lotteryRes);
            mjways2.money = mjways2.lotteryRes.curCoin;
            mjways2.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways2.exchangeRate,
                mjways2.lotteryRes.curCoin - mjways2.lotteryRes.winCoin);
            mjways2.resultIndex = 0;
            mjways2.roundIndex = 0;
            if (mjways2.slotCtrl.isSpeed) {
                this.roll(mjways2.round.symbols, mjways2.round.golds)
            } else {
                this.scheduleOnce(() => {
                    this.roll(mjways2.round.symbols, mjways2.round.golds)
                }, 1)
            }
        }
    }

    exchange(data: ResPlayMatch3) {
        let res = dataChange.change(data);
        mjways2.lotteryRes = res;
    }

    playVoxRandom() {
        this.schedule(this.doPlayVoxRandom, 10, macro.REPEAT_FOREVER)
    }

    private doPlayVoxRandom() {
        mjways2.audio.playVoxRandom();
    }

    stopVoxRandom() {
        this.unschedule(this.doPlayVoxRandom);
    }

    stopImmediately() {

    }

    close() {

    }
}