import { _decorator, Component, Node } from 'cc';
import { tween } from 'cc';
import { mjways2 } from './cMjways2';
import { labNumShow } from '../../module_basic/utils/labNumShow';
import { Num } from '../../core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('mjways2InfoBoard')
export class mjways2InfoBoard extends Component {
    @property({ type: [Node], tooltip: "底板背景" })
    public bgs: Node[] = [];
    @property({ type: Node, tooltip: "消息跑马灯" })
    public infos: Node = null!;
    @property({ type: Node, tooltip: "赢钱信息" })
    public infoWin: Node = null!;

    onLoad() {
        mjways2.infoBoard = this;
    }

    protected start(): void {
        this.lev = 0;
        this.showInfosBn = true;
    }

    private _lev: number = 0;
    public get lev(): number {
        return this._lev
    }
    public set lev(v: number) {
        this._lev = v;
        this.bgs[0].active = v == 0;
        this.bgs[1].active = v == 1;
        this.bgs[2].active = v == 2;
    }

    /**winNode label */
    private playBn: boolean = false;
    playAnimWin(coin: number, playBn: boolean = true, showTwBn: boolean = false) {
        this.showInfosBn = false;
        this.showTwBn = showTwBn;
        if (coin > mjways2.betSum * 3 && coin < mjways2.betSum * 5) {
            this.lev = 1;
        } else if (coin >= mjways2.betSum * 5) {
            this.lev = 2;
        } else {
            this.lev = 0;
        }
        this.playBn = playBn;
        let num: labNumShow = this.winLab;
        let bindWin: Function = () => {
            if (!showTwBn) {
                mjways2.tempWin += coin;
                mjways2.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways2.exchangeRate,
                    mjways2.lotteryRes.curCoin - mjways2.lotteryRes.winCoin + mjways2.tempWin);
                mjways2.slotCtrl.winCoin_lab.string = Num.exchangeToThousands(mjways2.exchangeRate, mjways2.tempWin);
            }
        }
        if (playBn) {
            num.setRealNum(0);
            tween(num)
                .to(0.5, { num: coin * 100 })
                .call(() => {
                    this.playBn = false;
                    bindWin();
                })
                .start();

        } else {
            bindWin();
            num.setRealNum(coin);
        }
    }

    private bindWinCoin() {
        let num: number = this.winLab.getRealNum();
        let addNum: number = num + mjways2.tempWin;
        mjways2.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways2.exchangeRate,
            mjways2.lotteryRes.curCoin - mjways2.lotteryRes.winCoin + addNum);
        mjways2.slotCtrl.winCoin_lab.string = Num.exchangeToThousands(mjways2.exchangeRate, addNum);
    }

    protected update(dt: number): void {
        if (this.playBn) this.bindWinCoin();
    }

    /**是否显示跑马灯 */
    private _showInfosBn: boolean = false;
    public get showInfosBn(): boolean {
        return this._showInfosBn
    }
    public set showInfosBn(v: boolean) {
        this._showInfosBn = v;
        this.infos.active = v;
        this.infoWin.active = !v;
        this.lev = 0;
    }

    /**是否显示共赢得 */
    private _showTwBn: boolean = false;
    public get showTwBn(): boolean {
        return this._showTwBn;
    }
    public set showTwBn(v: boolean) {
        this._showTwBn = v;
        this.infoWin.getChildByName("info_win").active = !v;
        this.infoWin.getChildByName("info_tw").active = v;
    }

    private get winLab(): labNumShow {
        return this.infoWin.getChildByName("Label").getComponent(labNumShow);
    }
}